import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
@Component({
  selector: 'app-header-titles',
  templateUrl: './header-titles.component.html',
  styleUrls: ['./header-titles.component.css']
})
export class HeaderTitlesComponent implements OnInit {


		titles:string[]=['Home','About','Profile','Résumé'];
  constructor(private router: Router) { }

  ngOnInit(): void {
  }

navigate(i :number){
	if(i===0)
this.router.navigate(['/']);
else
this.router.navigate([this.titles[i]]);

}

}
